.childContainer3 {
  display: flex;
  /* height: 850px; */
  margin-top: 20px;
  /* grid-template-columns: 60% 40%; */
  /* border: 1px solid green; */
  justify-content: space-evenly;
}
.leftInfo {
  border: 1px solid red;
  width: 60%;
}
.leftInfo button {
  width: 197px;
  height: 39px;
  border-radius: 30px;
  background-color: rgba(232, 20, 84, 0.06);
  color: #e81454;
  border: none;
}
.leftInfo h1 {
  font-size: 36px;
  margin-top: 25px;
  text-align: center;
}
.leftInfo p {
  line-height: 209.6%;
  margin-top: 35px;
}

.rightImage {
  text-align: center;
  width: 30%;
  border: 1px solid blue;
}

@media only screen and (max-width: 600px) {
  .childContainer3 {
    display: flex;
    flex-direction: column;
    margin-top: 20px;
  }

  .leftInfo p {
    line-height: 209.6%;
    margin-top: 35px;
  }

  .leftInfo {
    margin-top: 0px;
    /* border: 1px solid red; */
    padding: 15px;
    width: 100%;
  }

  .rightImage {
    display: none;
  }
}
