.childContainer5 {
  /* height: 450px; */
  width: 100%;
  margin-top: 180px;
  border-radius: 5px;
  background-color: #ffffff;
  display: flex;
  justify-content: space-evenly;
  /* border: 1px solid blue; */
}

.subContainer {
  width: 100%;
  /* height: 400px; */
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  /* border: 1px solid red; */
}

.aboutContainer {
  /* height: 200px; */
  width: 25%;
  /* margin-top: 50px; */
  border: 1px solid greenyellow;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: column;
}

.aboutDetails {
  /* height: 150px; */
  width: 100%;
  margin-top: 50px;
}

.contactContainer {
  height: 300px;

  width: 30%;

  /* border: 1px solid hotpink; */
}

.contactDetails {
  height: 300px;
  width: 100%;
  /* margin-top: 50px; */
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: column;
  border: 1px solid greenyellow;
}
.contactDetails p {
  /* margin-top: 20px; */
}
.childContainer5 h3 {
  margin-bottom: 10px;
}

.email {
  height: 300px;
  width: 25%;
  border: 1px solid lawngreen;
  display: flex;
  justify-content: space-around;
  flex-direction: column;
  align-items: center;
}
.email h3 {
  margin-top: 10px;
}
.email input {
  width: 100%;
  border-top: none;
  border-right: none;
  border-left: none;
  outline: none;
  border: 1px solid blue;
}

@media only screen and (max-width: 600px) {
  .childContainer5 {
    display: block;
    height: 570px;
    width: 100%;
    border: 1px solid blue;
  }

  .subContainer {
    display: flex;
    flex-direction: column;
    /* width: 100px; */
    /* border: 1px solid red; */
  }

  .email {
    width: 100%;
  }

  .childContainer5 h3 {
    margin-bottom: 10px;
    margin-left: 150px;
  }

  .childContainer5 p {
    margin-left: 150px;
  }

  .contactContainer {
    /* height: 200px; */
    /* width: 300px; */
    margin-top: 10px;
    width: 100%;

    /* border: 1px solid hotpink; */
  }

  .contactDetails {
    /* height: 150px; */
    /* width: 250px; */
    margin-top: 20px;
    /* border: 1px solid greenyellow; */
  }

  .aboutContainer p {
    width: 100%;
    padding: 20px 0 0 0;
  }

  .aboutContainer {
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
  }

  .aboutDetails {
    margin-top: 30px;
  }

  .contactContainer p {
    margin-top: 50px;
  }

  .email input {
    width: 80%;
  }

  .email {
    /* width: 470px; */
    width: 100%;
    height: 100%;
  }
}
